@import url(https://fonts.googleapis.com/css?family=Open+Sans|Mrs+Sheppards);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #e9e4cf;
  overflow-x: hidden;
}

div, h1, h2, h3, h4, h5, h6 {
  box-sizing: border-box;
}

.app {
  box-sizing: border-box;
  width: 100vw;
  min-height: 100vh;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.header {
  width: 60vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  
  background-color: #2a352f;

  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
}

.header > h1 {
  font-size: 100px;
  color: #d0c69f;
  font-family: Mrs Sheppards;
  letter-spacing: 3px;
  font-weight: 500;

  text-align: start;
}

.sub-header {
  width: 40vw;
  height: 100vh;
}

.win-rate,
.player-faction {
  width: 40vw;
  height: 50vh;
}

.win-rate {
  background-color: #e9e4cf;
}

.player-faction {
  background-color: #87ce71;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.player-faction .main-svg {
  margin-left: -15px;
  margin-top: -25px;
}

.content-header {
  width: 100%;
  padding: 30px 0;
  font-family: Open Sans;
  font-size: 30px;
  color: #2a352f;
}

.content {
  margin: 0 auto;
  width: 1280px;
  max-width: 100%;
  overflow-x: scroll;

  padding: 30px 20px;
  font-family: Open Sans;
  font-size: 16px;
}


.js-plotly-plot {
  display: block;
  width: 100%;
  height: 100%;
}


@media (max-width: 500px) {
  .header,
  .content,
  .win-rate,
  .player-faction { 
    width: 100vw;
    overflow-x: hidden;
  }

  .win-rate {
    display: none;
  }

  .player-faction .main-svg{
    margin-left: -58px;
  }

}
